
.Header {
    display: flex;
    justify-content: center;
    font-weight: 700;
    background-color: #F6EBDA;
    padding: 10px 0 5px 0;
    @media screen and (min-width: 768px) {
        padding: 10px 0; } }

.container {
    width: 100%;
    max-width: 1140px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 5px; }
.Logo {
    font-size: 16px;
    @media screen and (min-width: 480px) {
        font-size: 22px; }
    @media screen and (min-width: 768px) {
        font-size: 28px; }
    font-family: 'Dancing Script', cursive; }

.Nav {
    display: flex;
    font-weight: 400;
    padding-top: 3px; }

.Link {
    color: rgb(0, 0, 0);
    font-size: 12px;
    @media screen and (min-width: 480px) {
        font-size: 16px; }
    @media screen and (min-width: 768px) {
        font-size: 20px; }
    margin-right: 10px; }

.Btn {
    background: transparent;
    cursor: pointer;
    position: relative;
    margin-left: 5px;
    &>svg {
        width: 25px;
        height: 25px; }
    @media screen and (min-width: 768px) {
        &>svg {
            width: 32px;
            height: 32px; } }
    &:hover {
        & .BtnIndicator {
            background: #FF902A;
            color: black; } }
    &Indicator {
        transition: background 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        position: absolute;
        left: 55%;
        top: -10%;
        font-size: 10px;
        background: black;
        color: white;
        padding: 1px 5px;
        border-radius: 10px; } }

.Moon {
    &:hover {
        &>svg {
            fill: #FF902A; } } }

.Sun {
    &:hover {
        &>svg {
            fill: #3dcfe6; } } }

.Lang {
    font-size: 14px;
    @media screen and (min-width: 480px) {
        font-size: 16px; }
    @media screen and (min-width: 768px) {
        font-size: 20px; }
    margin-left: 5px;
    background: transparent;
    cursor: pointer;
    align-self: center;
    justify-self: center; }

.Ua {
    &:hover {
        & .U {
            color: blue; }
        & .A {
            color: rgb(255, 247, 0); } } }
